export class Card {
    private suit: number;
    private rank: number;
    /*
    * CLUBS = 1;
    * DIAMONDS = 2;
    * HEARTS = 3;
    * SPADES = 4;
    * DEUCE = 2;
    * THREE = 3;
    * FOUR = 4;
    * FIVE = 5;
    * SIX = 6;
    * SEVEN = 7;
    * EIGHT = 8;
    * NINE = 9;
    * TEN = 10;
    * JACK = 11;
    * QUEEN = 12;
    * KING = 13;
    * ACE = 14;
    */

    constructor(suit: number, rank: number) {
        if (this.isValidRank(rank) && this.isValidSuit(suit)) {
            this.rank = rank;
            this.suit = suit;
        } else {
            console.log(`Creat card failed suit ${suit} rank ${rank}`);
        }
    }

    public getSuit(): number {
        return this.suit;
    }

    public getRank(): number {
        return this.rank;
    }

    public printCard(): string {
        return `${this.rankToString(this.rank)}${this.suitToString(this.suit)}`;
    }

    public isValidRank(rank: number): boolean {
        return rank === 0 || 2 <= rank && rank <= 14;
    }

    public isValidSuit(suit: number): boolean {
        return suit === 0 || 1 <= suit && suit <= 4;
    }

    public rankToString(rank: number): string {
        switch (rank) {
            case 2:
                return '2';
            case 3:
                return '3';
            case 4:
                return '4';
            case 5:
                return '5';
            case 6:
                return '6';
            case 7:
                return '7';
            case 8:
                return '8';
            case 9:
                return '9';
            case 10:
                return '10';
            case 11:
                return 'J';
            case 12:
                return 'Q';
            case 13:
                return 'K';
            case 14:
                return 'A';
            default:
                return '0';
        }
    }

    public suitToString(suit: number): string {
        switch (suit) {
            case 1:
                return 'C';
            case 2:
                return 'D';
            case 3:
                return 'H';
            case 4:
                return 'S';
            default:
                return '0';
        }
    }
}
