import { Injectable } from '@angular/core';
import { NrOfSuits } from 'src/app/interfaces/nr-of-suits.interface';
import { SortedCards } from 'src/app/interfaces/sorted-cards.interface';
import {
  clubTwo,
  isSpecificCard,
  isSpecificCardIncluded,
  nullCard,
  spadeAce,
  spadeKing,
  spadeQueen
} from 'src/app/table-parts/card/cards';
import { Suit } from 'src/app/table-parts/suit/suit';
import { suits } from 'src/app/table-parts/suit/suits';
import { Card } from '../../table-parts/card/card';
import { HeartsPlayer } from '../../table-parts/player/hearts-player';
import { HeartsTable } from '../../table-parts/table/hearts-table';

@Injectable()
export class HeartsService {

  private readonly suitSwapPriority = [suits[2], suits[0], suits[1], suits[3]];
  private readonly throwOutPriority = [suits[2], suits[3], suits[1], suits[0]];

  constructor() {}

  moveToSwap (player: HeartsPlayer, cardToSwap: Card) {
    player.removeCard(cardToSwap);
    player.addCardToSwap(cardToSwap);
  }

  select3CardToSwap(player: HeartsPlayer) {
    const playerCards: SortedCards = player.getSortedCards();
    const takeAllStrategy = this.chooseTakeAllStrategy(playerCards);

    if (takeAllStrategy) {
      player.setStartegyTakeAll(true);

      this.chooseLowCardsToSwap(player, playerCards);
    } else {
      this.chooseHighCardsToSwap(player, playerCards);
    }
  }

  selectCardAI(myTable: HeartsTable, playerIdx: number): Card {
    const players = myTable.getPlayers();
    const cardsOnTable = myTable.checkCardsOnTable();
    const pointCardsOnTable = cardsOnTable.filter((card) => {
      return card.getSuit() === Suit.HEART || isSpecificCard(card, spadeQueen);
    });
    const player = myTable.getPlayer(playerIdx);
    const playerCards = player.getSortedCards();
    const clubs = playerCards.clubs;
    const clubsLength = playerCards.clubs.length;
    const diamonds = playerCards.diamonds;
    const diamondsLength = playerCards.diamonds.length;
    const spades = playerCards.spades;
    const spadesLength = playerCards.spades.length;
    const hearts = playerCards.hearts;
    const heartsLength = playerCards.hearts.length;
    const sortedCardsLength = player.getSortedCardsLength();
    const hasSpadeQueen = spadesLength ? isSpecificCardIncluded(spades, spadeQueen) : false;
    const haveOthersPoints = this.checkOthersPoints(players, playerIdx);
    const isStrategyTakeAll = this.checkStartegyTakeAll(player, haveOthersPoints);
    let selectedCard = nullCard;

    // ha els kr van
    if (sortedCardsLength === 13) {
      if (player.isStarter()) {
        selectedCard = clubTwo;
      } else {
        // ha van treff
        if (clubsLength) {
          selectedCard = isStrategyTakeAll ? clubs[0] : clubs[clubsLength - 1];
        // ha mindent vinni akar
        } else if (isStrategyTakeAll) {
          // ha van kr s pikkbl kisebb mint pikk Q vlassza a legkisebb krt vagy pikket
          if (diamondsLength && spadesLength && spades[0].getRank() < spadeQueen.getRank()) {
            const firstDiamond = diamonds[0];
            const firstSpade = spades[0];
            selectedCard = firstDiamond.getRank() <= firstSpade.getRank()
              ? firstDiamond
              : firstSpade;
          // ha van kr vlassza a legkisebbet
          } else if (diamondsLength) {
            selectedCard = diamonds[0];
          // ha csak pikk van vlassza a legkisebbet a pikk Q-n kvl vagy ha csak a pikk Q van akkor azt
          } else if (spadesLength) {
            const lastSpade = spades[0];
            selectedCard = isSpecificCard(selectedCard, lastSpade)
              ? spadesLength > 1
                ? spades[1]
                : lastSpade
              : lastSpade;
          // ha csak szv van akkor a legnagyobbat (alig van esly erre az esetre s itt biztosan nem is fog vinni semmit)
          } else {
            selectedCard = hearts[heartsLength - 1];
          }
        // ha nincs treff s nem akar mindent vinni
        } else {
          const lastSpade = spadesLength ? spades[spadesLength - 1] : nullCard;
          const isSpadeObligatory = !diamondsLength && lastSpade.getRank() > 0;
          // ha ktelez pikket tenni (nincs kr) vlassza a legnagyobbat, vagy ha van nla nagyobb, mint pikk Q
          if (isSpadeObligatory || (!hasSpadeQueen && lastSpade.getRank() > spadeQueen.getRank())) {
            selectedCard = isSpecificCard(selectedCard, lastSpade)
              ? spadesLength > 1
                ? spades[spadesLength - 2]
                : lastSpade
              : lastSpade;
          // ha van kr vlassza a legnagyobbat
          } else if (diamondsLength) {
            selectedCard = diamonds[diamondsLength - 1];
          // ha csak szv van akkor a legnagyobbat (alig van esly erre az esetre s itt biztosan nem is fog vinni semmit)
          } else {
            selectedCard = hearts[heartsLength - 1];
          }
        }
      }
    // ha nem az els kr van
    } else {
      let firstCard = nullCard;
      let openSuit = '';
      let suitsFromOpenSuit: Card[] = [];
      let lastCardFromOpenSuit = nullCard;
      let suitsOnTable: Card[] = [];
      // az asztalon lv legnagyobb krtya rtke az adott sznben
      let highestCardRankOnTable = 0;
      if (!myTable.isTableEmpty()) {
        firstCard = myTable.getFirstCard();
        openSuit = suits[firstCard.getSuit() - 1];
        suitsFromOpenSuit = playerCards[openSuit];
        lastCardFromOpenSuit = suitsFromOpenSuit[suitsFromOpenSuit.length - 1];
        suitsOnTable = cardsOnTable.filter((card) => card.getSuit() === firstCard.getSuit())
          .sort((a, b) => (a.getRank() > b.getRank()) ? 1 : -1);
        highestCardRankOnTable = suitsOnTable[suitsOnTable.length - 1].getRank();
      }

      // ha mindent akar vinni
      if (isStrategyTakeAll) {
        // ha a jtkos kezd
        if (player.isStarter()) {
          if (hasSpadeQueen) {
            selectedCard = spadeQueen;
          } else {
            selectedCard = player.findHighestCardInSortedCards([Suit.CLUB, Suit.DIAMOND, Suit.HEART, Suit.SPADE]);
            /* if (heartsLength) {
              selectedCard = hearts[heartsLength - 1];
            } else {
              selectedCard = player.findHighestCardInSortedCards([Suit.CLUB, Suit.DIAMOND, Suit.SPADE]);
            } */
          }
        // ha a jtkos nem kezd
        } else {
          // ha van a nyit sznben lapja
          if (suitsFromOpenSuit.length) {
            if (player.isLast()) {
              selectedCard = this.findHigherThanOpenSuitCard(suitsFromOpenSuit, lastCardFromOpenSuit, highestCardRankOnTable);
            } else {
              selectedCard = lastCardFromOpenSuit;
            }
          // ha nincs a nyit sznben lapja
          } else {
            if (pointCardsOnTable && (hasSpadeQueen || heartsLength)) {
              selectedCard = hasSpadeQueen
                ? spadeQueen
                : hearts[heartsLength - 1];
              player.setStartegyTakeAll(false);
            } else {
              if (clubsLength || diamondsLength) {
                selectedCard = player.findLowestCardInSortedCards([Suit.CLUB, Suit.DIAMOND]);
              } else if (spadesLength) {
                selectedCard = player.findLowestCardInSortedCards([Suit.SPADE]);
                if (isSpecificCard(selectedCard, spadeQueen)) {
                  player.setStartegyTakeAll(false);
                }
              } else {
                selectedCard = hearts[heartsLength - 1];
                player.setStartegyTakeAll(false);
              }
            }
          }
        }
      // ha nem akar mindent vinni
      } else {
        // ha nla van a pikk Q
        if (hasSpadeQueen) {
          // ha a jtkos kezd
          if (player.isStarter()) {
            if (spadesLength !== sortedCardsLength) {
              if (!myTable.isHeartsBroken()) {
                // ha van pikken kvli szne, akkor nyisson a legkisebb rtk lapjval
                if (clubsLength || diamondsLength) {
                  selectedCard = player.findLowestCardInSortedCards([Suit.CLUB, Suit.DIAMOND]);
                // ha csak pikk s szvszn krtyk maradtak
                } else if (spadesLength > 1) {
                  selectedCard = isSpecificCard(spades[0], spadeQueen)
                    ? spades[1]
                    : spades[0];
                } else if (spadesLength === 1) {
                  selectedCard = spadeQueen;
                } else {
                  selectedCard = hearts[0];
                }
              } else {
                selectedCard = player.findLowestCardInSortedCards([Suit.CLUB, Suit.DIAMOND, Suit.HEART]);
              }
            } else {
              selectedCard = spadesLength > 1 && isSpecificCard(spades[0], spadeQueen)
                ? spades[1]
                : spades[0];
            }
          // ha a jtkos nem kezd
          } else {
            // ha nincs a nyit sznben lapja s nem pikk a kezd szn
            // vagy a pikk K,A ki lett jtszva,
            // vagy pikk a kezd szn s csak pikk Q van nla, akkor pikk Q-t tesz
            if (!suitsFromOpenSuit.length
              || isSpecificCardIncluded(cardsOnTable, spadeAce)
              || isSpecificCardIncluded(cardsOnTable, spadeKing)
              || firstCard.getSuit() === Suit.SPADE && spadesLength === 1) {
              selectedCard = spadeQueen;
            // ha pikk a kezd szn s minimum kett van,
            // s van benne pont, vlassza azasztalon lv legnagyobbtl szmtott els kisebbet (de ne a pikk Q-t)
            // vagy nincs benne pont s vlassza a legnagyobbat (de ne a pikk Q-t),
            } else if (firstCard.getSuit() === Suit.SPADE && spadesLength > 1) {
              selectedCard = pointCardsOnTable.length
                ? this.findLowerThanOpenSuitCard(suitsFromOpenSuit, lastCardFromOpenSuit, highestCardRankOnTable)
                : isSpecificCard(lastCardFromOpenSuit, spadeQueen)
                  ? spades[spadesLength - 2]
                  : lastCardFromOpenSuit;
            // ha van a nyit sznben lapja s nem pikk a kezd szn
            } else if (suitsFromOpenSuit.length) {
              selectedCard = this.selectOpenSuitCard(
                !!pointCardsOnTable.length,
                highestCardRankOnTable,
                suitsFromOpenSuit,
                lastCardFromOpenSuit,
                player.isLast()
              );
            }
          }
        // ha nincs nla a pikk Q
        } else {
          // ha kezd nyisson a legkisebbel vagy ha mg nincs pikk Q akkor attl kisebb pikkel
          if (player.isStarter()) {
            const spadeHighs = (isSpecificCardIncluded(spades, spadeKing) ? 1 : 0) + (isSpecificCardIncluded(spades, spadeAce) ? 1 : 0);
            // ha a pikk Q mg nincs kijtszva s van kisebb pikkje
            if (!myTable.isSpadeQueenOut() && ((spadesLength - spadeHighs) > 1)) {
              selectedCard = this.findLowerThanOpenSuitCard(spades, spades[spadesLength - 1], spadeQueen.getRank());
            // ha a pikk Q kijtszva vagy nincs csak nagyobb pikkje, akkor nyisson a legkisebb rtk lapjval
            } else  {
              if (!myTable.isHeartsBroken()) {
                // ha van pikken kvli szne, akkor nyisson a legkisebb rtk lapjval
                if (clubsLength || diamondsLength) {
                  selectedCard = player.findLowestCardInSortedCards([Suit.CLUB, Suit.DIAMOND]);
                // ha csak pikk s szvszn krtyk maradtak
                } else if (spadesLength > 1) {
                  selectedCard = spades[0];
                } else {
                  selectedCard = hearts[0];
                }
              } else {
                selectedCard = player.findLowestCardInSortedCards([Suit.CLUB, Suit.DIAMOND, Suit.HEART, Suit.SPADE]);
              }
            }
          // ha a jtkos nem kezd
          } else {
            // ha van a nyit sznben lapja
            if (suitsFromOpenSuit.length) {
              selectedCard = this.selectOpenSuitCard(
                !!pointCardsOnTable.length,
                highestCardRankOnTable,
                suitsFromOpenSuit,
                lastCardFromOpenSuit,
                player.isLast()
              );
            // ha nincs a nyit sznben lapja
            } else {
              // tehet pikk K,A-t, legnagyobb szvet, legnagyobb sznt
              if (isSpecificCardIncluded(spades, spadeAce)) {
                selectedCard = spadeAce;
              } else if (isSpecificCardIncluded(spades, spadeKing)) {
                selectedCard = spadeKing;
              } else {
                for (let i = 0; selectedCard === nullCard && i < this.throwOutPriority.length; i++) {
                  const suit = this.throwOutPriority[i];
                  const suitCards = playerCards[suit];
                  const suitCardsLength = suitCards.length;

                  if (suitCardsLength) {
                    selectedCard = suitCards[suitCardsLength - 1];
                  }
                }
              }
            }
          }
        }
      }
    }

    if (selectedCard === nullCard) {
      console.error(`${player.getName()} did not select any card!`);
    }

    return selectedCard;
  }

  private checkOthersPoints(players: HeartsPlayer[], playerIdx: number): boolean {
    let allOthersPointsInTurn = 0;
    for (let i = 0; i < players.length; i++) {
      if (i !== playerIdx) {
        const playerPointsInTurn = players[i].getReal();
        if (playerPointsInTurn) {
          allOthersPointsInTurn += playerPointsInTurn;
        }
      }
    }

    return !!allOthersPointsInTurn;
  }

  private checkStartegyTakeAll(player: HeartsPlayer, haveOthersPoints: boolean): boolean {
    let isStrategyTakeAll = player.isStartegyTakeAll();
    if (haveOthersPoints) {
      isStrategyTakeAll = false;
      player.setStartegyTakeAll(false);
    } else if (player.getReal() >= 13) {
      isStrategyTakeAll = true;
      player.setStartegyTakeAll(true);
    }

    return isStrategyTakeAll;
  }

  private hasAllHeartHighCards(hearts: Card[]): boolean {
    if (hearts.length < 4) {
      return false;
    }

    // ha szvbl megvan a J,Q,K,A
    return hearts[hearts.length - 1].getRank() === 14
      && hearts[hearts.length - 2].getRank() === 13
      && hearts[hearts.length - 3].getRank() === 12
      && hearts[hearts.length - 4].getRank() === 11;
  }

  // ha megvan a pikk Q s a szv J,Q,K,A, akkor prblhat all in-re menni
  private chooseTakeAllStrategy(playerCards: SortedCards): boolean {
    const spades = playerCards.spades;
    const spadesLength = spades.length;
    const hasSpadeQueen = spadesLength ? isSpecificCardIncluded(spades, spadeQueen) : false;

    return hasSpadeQueen
      ? this.hasAllHeartHighCards(playerCards.hearts)
      : false;
  }

  private swapLowestCards(player: HeartsPlayer, remainNrOfSuitsToSwap: number, suitsToSwap: number[]) {
    for (let i = 0; i < remainNrOfSuitsToSwap; i++) {
      const lowestCard = player.findLowestCardInSortedCards(suitsToSwap);
      this.moveToSwap(player, lowestCard);
    }
  }

  // kivlogatja a legkisebb lapokat, amiket el kell dobni
  private chooseLowCardsToSwap(player: HeartsPlayer, playerCards: SortedCards) {
    const clubsLength = playerCards.clubs.length;
    const diamondsLength = playerCards.diamonds.length;
    const spadesLength = playerCards.spades.length;
    const hearts = playerCards.hearts;
    const heartsLength = playerCards.hearts.length;

    let nrOfHeartsToSwap = 0;
    if (heartsLength < 8) {
      for (let i = 0; i < hearts.length - 3; i++) {
        if (hearts[i].getRank() < 9) {
          nrOfHeartsToSwap += 1;
        }
      }
    }
    let remainNrOfSuitsToSwap = 3;

    if (nrOfHeartsToSwap > 0) {
      remainNrOfSuitsToSwap -= nrOfHeartsToSwap;
      this.cardsToSwapBySuit(player, hearts, nrOfHeartsToSwap, true);
    }
    // A maradk sznekbl a legkisebbeket
    if (remainNrOfSuitsToSwap > 0) {
      this.swapLowestCards(player, remainNrOfSuitsToSwap, [Suit.CLUB, Suit.DIAMOND, Suit.SPADE]);
    }
    // ha a maradk hrom sznbl nem lenne meg a maradk cserlend pl.: maradk 3, szven kvli sznek 2
    const lastNrOfSuitsToSwap = remainNrOfSuitsToSwap - clubsLength - diamondsLength - spadesLength;
    if (lastNrOfSuitsToSwap > 0) {
      this.swapLowestCards(player, lastNrOfSuitsToSwap, [Suit.HEART]);
    }
  }

  private highCardsToThrow(cardsBySuit: Card[], cardRanksToHold: number[]): number {
    const cardsLength = cardsBySuit.length;
    let cardsToHold = 0;
    for (let i = 0; i < cardsLength && i < 3; i++) {
      const cardRank = cardsBySuit[i].getRank();
      if (cardRanksToHold.includes(cardRank)) {
        cardsToHold += 1;
      }
    }

    return cardsLength - cardsToHold;
  }

  private cardsToSwapBySuit(player: HeartsPlayer, cardsBySuit: Card[], nrOfSuitsToSwap: number, low: boolean) {
      let i = low ? 0 : cardsBySuit.length - 1;
      for (i; low ? i < nrOfSuitsToSwap : i >= (cardsBySuit.length - nrOfSuitsToSwap); low ? i++ : i--) {
        const cardToSwap = cardsBySuit[i];
        this.moveToSwap(player, cardToSwap);
      }
  }

  private swapRemainNrOfSuits(
    player: HeartsPlayer,
    sortedPlayerCards: SortedCards,
    suitsToSwap: string[],
    remainNrOfSuits: number,
  ) {
    let remainToSwap = remainNrOfSuits;
    for (let i = 0; remainToSwap > 0 && i < suitsToSwap.length; i++) {
      const suit = suitsToSwap[i];
      const suitCards = sortedPlayerCards[suit];
      const suitCardsLength = suitCards.length;
      const cardsToHold = [ 2, 3 ];
      const highCardsToThrow = this.highCardsToThrow(suitCards, cardsToHold);
      if (highCardsToThrow
        && (suit !== suits[2]
          || (suit === suits[2] && (suitCardsLength <= 5 || suitCardsLength >= 11)))) {
          const nrOfSuitToSwap = highCardsToThrow >= remainNrOfSuits
            ? remainNrOfSuits
            : highCardsToThrow;
          this.cardsToSwapBySuit(player, suitCards, nrOfSuitToSwap, false);
          remainToSwap -= nrOfSuitToSwap;
      }
    }
    if (remainToSwap > 0) {
      this.swapRemainNrOfSuits(player, sortedPlayerCards, this.suitSwapPriority, remainToSwap);
    }
  }

  private swapNrOfSuits(
    player: HeartsPlayer,
    sortedPlayerCards: SortedCards,
    suitsFromRemove: string[],
    highCardsToThrow: NrOfSuits,
  ) {
    let swappedNrOfSuits = 0;
    if (suitsFromRemove.length) {
      for (let i = 0; i < this.suitSwapPriority.length; i++) {
        if (swappedNrOfSuits < 3) {
          const suitToSwap = this.suitSwapPriority[i];
          let nrOfSuitsToSwap = 3 - swappedNrOfSuits;

          if (suitsFromRemove.includes(suitToSwap)) {
            const nrOfSuit = highCardsToThrow[suitToSwap];

            nrOfSuitsToSwap = nrOfSuitsToSwap >= nrOfSuit
              ? nrOfSuit
              : nrOfSuitsToSwap;
            this.cardsToSwapBySuit(player, sortedPlayerCards[suitToSwap], nrOfSuitsToSwap, false);
            swappedNrOfSuits += nrOfSuitsToSwap;
          }
        }
      }
    }
    if (swappedNrOfSuits < 3) {
      const remainNrOfSuits = 3 - swappedNrOfSuits;
      const suitsToSwap = this.suitSwapPriority.filter(suit => !suitsFromRemove.includes(suit));
      this.swapRemainNrOfSuits(player, sortedPlayerCards, suitsToSwap, remainNrOfSuits);
    }
  }

  // ha egy sznbl van 3 vagy kevesebb azt cserlje szn priorits szerint, a 2,3,4-es krtykat tartsa meg
  private chooseHighCardsToSwap(player: HeartsPlayer, sortedPlayerCards: SortedCards) {
    const cardsToHold = [ 2, 3, 4 ];
    const highCardsToThrow: NrOfSuits = {
      clubs: this.highCardsToThrow(sortedPlayerCards.clubs, cardsToHold),
      diamonds: this.highCardsToThrow(sortedPlayerCards.diamonds, cardsToHold),
      hearts: this.highCardsToThrow(sortedPlayerCards.hearts, cardsToHold),
      spades: this.highCardsToThrow(sortedPlayerCards.spades, cardsToHold),
    };
    const suitsFromRemove: string[] = [];

    Object.keys(highCardsToThrow).forEach(suit => {
      if (highCardsToThrow[suit] && highCardsToThrow[suit] <= 3) {
        suitsFromRemove.push(suit);
      }
    });

    this.swapNrOfSuits(player, sortedPlayerCards, suitsFromRemove, highCardsToThrow);
  }

  private findHigherThanOpenSuitCard(suitsFromOpenSuit: Card[], lastCardFromOpenSuit: Card, highestCardRankOnTable: number): Card {
    let selectedCard = suitsFromOpenSuit[0];
    if (suitsFromOpenSuit.length > 1 && lastCardFromOpenSuit.getRank() > highestCardRankOnTable) {
        selectedCard = lastCardFromOpenSuit;
        for (let i = suitsFromOpenSuit.length - 1; i >= 0; i--) {
          const nextCard = suitsFromOpenSuit[i];
          if (nextCard.getRank() > highestCardRankOnTable) {
            selectedCard = nextCard;
          }
        }

    }

    return selectedCard;
  }

  private findLowerThanOpenSuitCard(suitsFromOpenSuit: Card[], lastCardFromOpenSuit: Card, highestCardRankOnTable: number): Card {
    let selectedCard = lastCardFromOpenSuit;
    if (lastCardFromOpenSuit.getRank() > highestCardRankOnTable) {
        let lowerRankFound = false;
        for (let i = suitsFromOpenSuit.length - 1; !lowerRankFound && i >= 0; i--) {
          const nextCard = suitsFromOpenSuit[i];
          if (nextCard.getRank() < highestCardRankOnTable || i === 0) {
            selectedCard = nextCard;
            lowerRankFound = true;
          }
        }

    }

    return selectedCard;
  }

  private selectOpenSuitCard (
    hasPointCardsOnTable: boolean,
    highestCardRankOnTable: number,
    suitsFromOpenSuit: Card[],
    lastCardFromOpenSuit: Card,
    isLast: boolean,
  ): Card {
    let selectedCard = nullCard;

    // ha a jtkos utols
    // s van az asztalon pontot r krtya prbljon kisebbet tenni, ha nem tud a legnagyobbat
    // vagy nincs az asztalon pontot r krtya s a legnagyobbat teszi
    if (isLast) {
      selectedCard = hasPointCardsOnTable
        ? this.findLowerThanOpenSuitCard(suitsFromOpenSuit, lastCardFromOpenSuit, highestCardRankOnTable)
        : lastCardFromOpenSuit;
    // ha a jtkos nem utols
    } else {
      // tegye a kintlvknl kisebbet, ha tudja, ha nem a legnagyobbat
      selectedCard = this.findLowerThanOpenSuitCard(suitsFromOpenSuit, lastCardFromOpenSuit, highestCardRankOnTable);
    }

    return selectedCard;
  }
}
