import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { CommonModule, registerLocaleData } from '@angular/common';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import hu from '@angular/common/locales/hu';
import { FormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatInputModule } from '@angular/material/input';
import { MatSliderModule } from '@angular/material/slider';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatTabsModule } from '@angular/material/tabs';

import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { NgxWebstorageModule } from 'ngx-webstorage';

import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { RikikiComponent } from './games/rikiki/rikiki.component';
import { HeartsComponent } from './games/hearts/hearts.component';

import { RikikiService } from './games/rikiki/rikiki.service';
import { HeartsService } from './games/hearts/hearts.service';
import { HomeComponent } from './home/home.component';

registerLocaleData(hu, 'hu');

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    RikikiComponent,
    HeartsComponent,
  ],
  imports: [
    AppRoutingModule,
    BrowserModule,
    BrowserAnimationsModule,
    CommonModule,
    FormsModule,
    NgxWebstorageModule.forRoot({
      prefix: 'app-',
      caseSensitive: true,
    }),
    MatCardModule,
    MatButtonModule,
    MatInputModule,
    MatTabsModule,
    MatSliderModule,
    MatSlideToggleModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient],
      },
    }),
  ],
  providers: [RikikiService, HeartsService],
  bootstrap: [AppComponent]
})
export class AppModule {}
