import { Injectable } from '@angular/core';
import { suits } from 'src/app/table-parts/suit/suits';
import { Card } from '../../table-parts/card/card';
import { RikikiPlayer } from '../../table-parts/player/rikiki-player';
import { RikikiTable } from '../../table-parts/table/rikiki-table';

@Injectable()
export class RikikiService {

  constructor() {}

  private betAIRound1(
    myTable: RikikiTable,
    nrOfPlayers: number,
    bettingPlayerIdx: number,
    bettingPlayer: RikikiPlayer,
    totalBets: number,
    trumpCardSuit: number,
    bettingPlayerLast: boolean
  ): number {
    let bet: number;
    if (bettingPlayerLast) {
      if (totalBets === 1) {
        return 1; // az sszfogads nem egyezhet a krszmmal
      } else if (totalBets === 0) {
        return 0; // az sszfogads nem egyezhet a krszmmal
      }
    }

    bet = -1;
    for (let playerIdx = 0; bet !== 0 && playerIdx < nrOfPlayers; ++playerIdx) {
      if (playerIdx !== bettingPlayerIdx
        && trumpCardSuit === myTable.getPlayer(playerIdx).getCards()[0].getSuit()) {
        bet = 0; // van msnl adu
      }
    }

    if (bet < 0) {
      const seenBigSuits: number[] = []; // adun kvli ms sznben lv nagy lapok (nincs msnl adu)
      for (let playerIdx = 0; playerIdx < nrOfPlayers; ++playerIdx) {
        if (playerIdx !== bettingPlayerIdx
          && myTable.getPlayer(playerIdx).getCards()[0].getRank() > 9) {
          const otherPlayerSuit = myTable.getPlayer(playerIdx).getCards()[0].getSuit();
          if (!seenBigSuits.includes(otherPlayerSuit)) {
            seenBigSuits.push(otherPlayerSuit);
          }
        }
      }

      if (seenBigSuits.length === 3) {
        bet = 0;
      } else if (bettingPlayer.isStarter()) {
        bet = 1; // nincs msnl adu vagy adun kvli hrom klnbz sznben lv nagy lapok s a jtkos az els fogad
      } else {
        for (let playerIdx = 0; playerIdx < nrOfPlayers; ++playerIdx) {
          if (playerIdx !== bettingPlayerIdx
            && myTable.getPlayer(playerIdx).isStarter()
            && myTable.getPlayer(playerIdx).getBet() === 0) {
            // nincs msnl adu vagy adun kvli hrom klnbz sznben lv nagy lapok
            // s az els jtkos nem fogadott magra
            bet = 1;
          }
        }

        if (bet < 0) {
          bet = 0;
        }
      }
    }

    return bet;
  }

  private betAIRound2(
    myTable: RikikiTable,
    nrOfPlayers: number,
    round: number,
    bettingPlayerCards: Card[],
    totalBets: number,
    trumpCardSuit: number,
    bettingPlayerLast: boolean
  ): number {
    let bet = 0;
    const cardsIn = [true, true];
    let totalCardsOut = 0;
    let highTrumpCards = 0;
    let lowTrumpCards = 0;

    // adu krtya keress
    for (let i = 0; i < round; ++i) {
      if (trumpCardSuit === bettingPlayerCards[i].getSuit()) {
        if (bettingPlayerCards[i].getRank() > 9) {
          ++bet;
          ++highTrumpCards;
          ++totalCardsOut;
          cardsIn[i] = false;
        } else if (bettingPlayerCards[i].getRank() <= 9) {
          if (lowTrumpCards === 0) {
            ++bet;
          }
          ++lowTrumpCards;
          ++totalCardsOut;
          cardsIn[i] = false;
        }
      }
    }

    // ha van mg fogadhat lap, akkor ha van egy magas adu s mellette egy magas lap, akkor plusz egy ts
    if (totalCardsOut !== 2) {
      for (let i = 0; i < round; ++i) {
        if (cardsIn[i]
          && highTrumpCards > 0
          && bettingPlayerCards[i].getRank() > 10) {
          ++bet;
          ++totalCardsOut;
          cardsIn[i] = false;
        }
      }
    }

    // ha a fogads idig nulla, akkor kt nem adu szn magas lapra, ha az sszegk nagyobb mint 23
    // pl. minimum J + Q komb, akkor a fogads 1
    if (bet === 0
      && bettingPlayerCards[0].getRank() >= 11
      && bettingPlayerCards[1].getRank() >= 11
      && bettingPlayerCards[0].getRank() + bettingPlayerCards[1].getRank() > 23) {
      ++bet;
      // az sszfogads nem egyezhet a krszmmal ezrt vissza kell vonni egy tst
      if (bettingPlayerLast && totalBets === 1) {
        --bet;
      }
    } else if (totalCardsOut === 2) {
      if (bettingPlayerLast) {
        // kt magas vagy egy magas s egy alacsony adura vagy egy magas adura s egy magas lapra a fogads 2 volt,
        // de az sszfogads 0 s az sszegk nem egyezhet a krszmmal
        if (totalBets === 0 && lowTrumpCards !== 2) {
          --bet;
        } else if (totalBets === 1 && lowTrumpCards === 2) {
        // kt alacsony adura a fogads 1 volt, de az sszfogads is 1 s az sszegk nem egyezhet a krszmmal
          ++bet;
        }
      }
    } else {
      // ha legalbb egy adu van
      if (totalCardsOut === 1) {
        // egy alacsony adu esetn a fogads 1, de ha megelzi 2-es fogads, akkor 0-t fogad
        if (lowTrumpCards === 1) {
          for (let i = 0; i < nrOfPlayers; ++i) {
            if (myTable.getPlayer(i).getBet() === 2) {
              --bet;
            }
          }
        }
        // egy magas adu esetn a fogads 1
        // vagy egy alacsony adu esetn, ha nem elzi meg 2-es fogads, akkor a fogads 1
        // de az sszfogads is 1 s az sszegk nem egyezhet a krszmmal
        if (bettingPlayerLast && totalBets === 1 && bet === 1) {
          if (highTrumpCards > 0) {
            ++bet;
          } else {
            --bet;
          }
        }
      }
      // rossz lapok vagy alacsony adura fogads visszavonsa (megelzi 2-es fogads) esetn a fogads 0,
      // de az sszfogads 2 s az sszegk nem egyezhet a krszmmal
      if (bettingPlayerLast && totalBets === 2 && bet === 0) {
        ++bet;
      }
    }

    return bet;
  }

  private betAIRound3OrHigher(
    nrOfPlayers: number,
    round: number,
    bettingPlayerCards: Card[],
    bettingPlayerLast: boolean,
    totalBets: number,
    trumpCardSuit: number
  ): number {
    const cardsIn: boolean[] = [];
    let bet = 0;
    let kings = 0;
    let totalCardsOut = 0;
    let highTrumpCards = 0;
    let middleTrumpCards = 0;
    let lowTrumpCards = 0;

    for (let i = 0; i < round; i++) {
      cardsIn.push(true);
    }

    // adu krtya keress
    for (let i = 0; i < round; ++i) {
      if (trumpCardSuit === bettingPlayerCards[i].getSuit()
        && bettingPlayerCards[i].getRank() > 11) {
          ++bet;
          ++highTrumpCards;
          ++totalCardsOut;
          cardsIn[i] = false;
      }
    }

    for (let i = 0; i < round; ++i) {
      if (trumpCardSuit === bettingPlayerCards[i].getSuit()
        && bettingPlayerCards[i].getRank() <= 11
        && bettingPlayerCards[i].getRank() >= 9) {
        if ((highTrumpCards > 1 || middleTrumpCards < 2) && round * nrOfPlayers <= 36) {
          ++bet;
          ++totalCardsOut;
          cardsIn[i] = false;
        }
        ++middleTrumpCards;
      }
    }

    for (let i = 0; i < round; ++i) {
      if (trumpCardSuit === bettingPlayerCards[i].getSuit()
        && bettingPlayerCards[i].getRank() < 9) {
        if (lowTrumpCards !== 0 && (lowTrumpCards + 1) % 2 === 0 && round * nrOfPlayers <= 36) {
          ++bet;
          ++totalCardsOut;
          cardsIn[i] = false;
        }
        ++lowTrumpCards;
      }
    }

    // ha van mg fogadhat lap, akkor ha van egy magas adu s mellette egy magas lap, akkor plusz egy ts
    if (totalCardsOut !== round) {
      let remainingTrumpCards = (middleTrumpCards + lowTrumpCards) - (totalCardsOut - highTrumpCards);
      for (let i = 0; i < round; ++i) {
        if (cardsIn[i]
          && trumpCardSuit !== bettingPlayerCards[i].getSuit()
          && bettingPlayerCards[i].getRank() > 11
          && remainingTrumpCards > 0) {
          ++bet;
          ++totalCardsOut;
          cardsIn[i] = false;
          --remainingTrumpCards;
        }
      }
    }

    if (bet === 0) {
      for (let i = 0; i < round; ++i) {
        if (bettingPlayerCards[i].getRank() === 14 && round < 5 && nrOfPlayers < 5) {
          ++bet;
        }
        if (bettingPlayerCards[i].getRank() === 13 && round < 5 && nrOfPlayers < 5) {
          ++bet;
          ++kings;
        }
      }
    }

    if (bettingPlayerLast && (totalBets + bet === round)) {
      if (bet === round) {
        --bet;
      } else if (bet === 0) {
        ++bet;
      } else {
        if (highTrumpCards === bet) {
          ++bet;
        } else if (kings > 0) {
          --bet;
        } else {
          --bet;
        }
      }
    }

    return bet;
  }

  betAI(
    myTable: RikikiTable,
    trumpCard: Card,
    nrOfPlayers: number,
    round: number,
    bettingPlayerIdx: number
  ): number {
    const bettingPlayer = myTable.getPlayer(bettingPlayerIdx);
    const bettingPlayerCards = bettingPlayer.getCards();
    const totalBets = myTable.getBetTotal();
    const trumpCardSuit = trumpCard.getSuit();
    const bettingPlayerLast = bettingPlayer.isLast();
    if (round === 1) {
      return this.betAIRound1(
        myTable,
        nrOfPlayers,
        bettingPlayerIdx,
        bettingPlayer,
        totalBets,
        trumpCardSuit,
        bettingPlayerLast
      );
    } else if (round === 2) {
      return this.betAIRound2(
        myTable,
        nrOfPlayers,
        round,
        bettingPlayerCards,
        totalBets,
        trumpCardSuit,
        bettingPlayerLast
      );
    } else {
      return this.betAIRound3OrHigher(
        nrOfPlayers,
        round,
        bettingPlayerCards,
        bettingPlayerLast,
        totalBets,
        trumpCardSuit
      );
    }
  }

  private tryToBeatTrumpCard(
    myTable: RikikiTable,
    player: RikikiPlayer,
    playerCards: Card[],
    trumpCardSuit: number,
  ): number {
    let cardIdx = player.findHighestTrumpCard(trumpCardSuit);
    const cardsOnTable = myTable.checkCardsOnTable();
    // Az asztalon lv adu krtyknl van-e nagyobb adukrtyja a jtkosnak
    // ha nincs a legkisebbet teszi
    if (cardIdx !== -1) {
      let higherRankFound = false;
      for (let i = 0; !higherRankFound && i < cardsOnTable.length; ++i) {
        if (cardsOnTable[i].getSuit() === trumpCardSuit
          && cardsOnTable[i].getRank() > playerCards[cardIdx].getRank()) {
          cardIdx = player.findLowestTrumpCard(trumpCardSuit);
          higherRankFound = true;
        }
      }
    }

    return cardIdx;
  }

  selectCardAI(
    myTable: RikikiTable,
    trumpCard: Card,
    round: number,
    playerIdx: number
  ): number {
    const trumpCardSuit = trumpCard.getSuit();
    const player = myTable.getPlayer(playerIdx);
    const playerCards = player.getCards();
    const playerBet = player.getBet();
    let cardIdx = -1;
    let firstCardSuit = -1;

    if (player.isStarter()) {
      if (playerBet === round) {
        cardIdx = player.findHighestTrumpCard(trumpCardSuit);
        if (cardIdx === -1) {
          cardIdx = player.findPlayerHighestCard();
        }

        return cardIdx;
      }

      if (playerBet <= 1) {
        cardIdx = player.findHighestTrumpCard(trumpCardSuit);
        if (cardIdx > -1) {
          cardIdx = player.findLowestCard(trumpCardSuit, trumpCardSuit);
        }
        if (cardIdx === -1) {
          cardIdx = player.findPlayerLowestCard();
        }

        return cardIdx;
      }
    }

    if (!myTable.isTableEmpty()) {
      firstCardSuit = myTable.getFirstCard().getSuit();
    }

    if (playerBet <= 0) {
      if (playerBet === 0) {
        for (let i = 0; cardIdx === -1 && i < playerCards.length; ++i) {
          if (firstCardSuit !== trumpCardSuit && firstCardSuit === playerCards[i].getSuit()) {
            cardIdx = player.findLowestSuitCard(firstCardSuit);
          }
        }

        if (firstCardSuit === trumpCardSuit
          || (firstCardSuit !== trumpCardSuit && player.getSortedCards()[suits[trumpCardSuit - 1]].length)) {
          cardIdx = player.findLowestTrumpCard(trumpCardSuit);
        }

        if (cardIdx === -1) {
          cardIdx = player.findHighestCard(trumpCardSuit, firstCardSuit);
        }
      }
    } else {
      // ha legalbb egy ts lett fogadva
      // a nyit sznben, ha az nem adu, tegye a legnagyobbat
      if (firstCardSuit !== trumpCardSuit) {
        for (let i = 0; cardIdx === -1 && i < playerCards.length; ++i) {
          if (firstCardSuit === playerCards[i].getSuit()) {
            cardIdx = player.findHighestSuitCard(firstCardSuit);
          }
        }
      // ha a nyit szn adu
      } else if (firstCardSuit === trumpCardSuit) {
        let highTrumpCards = 0;
        for (let i = 0; i < playerCards.length; ++i) {
          if (trumpCardSuit === playerCards[i].getSuit() && playerCards[i].getRank() > 9) {
            highTrumpCards += 1;
          }
        }
        if (highTrumpCards > 2) {
          cardIdx = this.tryToBeatTrumpCard(myTable, player, playerCards, trumpCardSuit);
        } else {
          cardIdx = player.findLowestTrumpCard(trumpCardSuit);
        }
      }
      // ha nincs a nyit sznben lapja s a nyit szn nem adu
      if (cardIdx === -1) {
        cardIdx = this.tryToBeatTrumpCard(myTable, player, playerCards, trumpCardSuit);
      }
      // ha nincs a nyit sznben lapja s a nyit szn nem adu s nincs aduja, tegye a legkisebb krtyt
      if (cardIdx === -1) {
        cardIdx = player.findLowestCard(trumpCardSuit, firstCardSuit);
      }
    }

    return cardIdx;
  }

}
