import { SortedCards } from 'src/app/interfaces/sorted-cards.interface';
import { Card } from '../card/card';
import { Suit } from '../suit/suit';
import { suits } from '../suit/suits';

export abstract class Player {
  protected name: string;
  protected cards: Card[] = [];
  protected sortedCards: SortedCards = {
    clubs: [],
    diamonds: [],
    hearts: [],
    spades: [],
  };
  protected bet = -1;
  protected real = 0;
  protected score = 0;
  protected starter = false;
  protected last = false;

  constructor(name: string) {
    this.name = name;
  }

  getName(): string {
    return this.name;
  }

  setName(name: string) {
    this.name = name;
  }

  hasCard(): boolean {
    return !!this.cards.length;
  }

  addCard(card: Card) {
    const cardSuitArray = this.sortedCards[suits[card.getSuit() - 1]];
    let inserted = false;
    if (!cardSuitArray.length) {
      cardSuitArray.push(card);
    } else {
      for (let i = 0; !inserted && i < cardSuitArray.length; i++) {
        if (card.getRank() < cardSuitArray[i].getRank()) {
          inserted = true;
          cardSuitArray.splice(i, 0, card);
        }
      }
      if (!inserted) {
        cardSuitArray.push(card);
      }
    }
  }

  removeCardByIdx(indexOf: number): Card {
    const cardToRemove = this.cards.splice(indexOf, 1)[0];
    const suit = suits[cardToRemove.getSuit() - 1];
    const properSuitsArray = this.sortedCards[suit];
    this.sortedCards[suit] = properSuitsArray.filter((card: { getRank: () => number; }) => card.getRank() !== cardToRemove.getRank());

    return cardToRemove;
  }

  getCards(): Card[] {
    return this.cards;
  }

  getSortedCards(): SortedCards {
    return this.sortedCards;
  }

  getSortedCardsLength(): number {
    return this.sortedCards.clubs.length
      + this.sortedCards.diamonds.length
      + this.sortedCards.hearts.length
      + this.sortedCards.spades.length;
  }

  sortPlayerCards(suitOrder: number[]) {
    this.cards = [
      ...this.sortedCards[suits[suitOrder[0] - 1]],
      ...this.sortedCards[suits[suitOrder[1] - 1]],
      ...this.sortedCards[suits[suitOrder[2] - 1]],
      ...this.sortedCards[suits[suitOrder[3] - 1]]
    ];
  }

  private findCard(trumpSuit?: number, playerSuit?: number, highestCardRank?: number, lowestCardRank?: number): number {
    let cardIndex = -1;

    for (let i = 0; i < this.cards.length; ++i) {
      const currentSuit = this.cards[i].getSuit();
      const currentRank = this.cards[i].getRank();
      const highestRank = highestCardRank !== undefined && highestCardRank < currentRank;
      const lowestRank = lowestCardRank !== undefined && lowestCardRank > currentRank;
      if (trumpSuit === undefined || playerSuit === undefined) {
        const suit = trumpSuit !== undefined ? trumpSuit : playerSuit;
        if (suit === currentSuit) {
          if (highestRank) {
            highestCardRank = currentRank;
            cardIndex = i; // highest suit
          } else if (lowestRank) {
            lowestCardRank = currentRank;
            cardIndex = i; // lowest suit
          }
        }
      } else if (trumpSuit !== currentSuit && playerSuit !== currentSuit) {
          if (highestRank) {
            highestCardRank = currentRank;
            cardIndex = i;
          } else if (lowestRank) {
            lowestCardRank = currentRank;
            cardIndex = i;
          }
      }
    }

    return cardIndex;
  }

  findHighestTrumpCard(trumpSuit: number): number {
    return this.findCard(trumpSuit, undefined, 0, undefined);
  }

  findLowestTrumpCard(trumpSuit: number): number {
    return this.findCard(trumpSuit, undefined, undefined, 15);
  }

  findHighestSuitCard(playerStarterSuit: number): number {
    return this.findCard(undefined, playerStarterSuit, 0, undefined);
  }

  findLowestSuitCard(playerStarterSuit: number): number {
    return this.findCard(undefined, playerStarterSuit, undefined, 15);
  }

  findHighestCard(trumpCardSuit: number, playerStarterSuit: number): number {
    return this.findCard(trumpCardSuit, playerStarterSuit, 0, undefined);
  }

  findLowestCard(trumpCardSuit: number, playerStarterSuit: number): number {
    return this.findCard(trumpCardSuit, playerStarterSuit, undefined, 15);
  }

  findPlayerHighestCard(): number {
    let cardIndex = -1;
    let highestCardRank = 0;

    for (let i = 0; i < this.cards.length; ++i) {
      const currentRank = this.cards[i].getRank();
      if (highestCardRank < currentRank) {
        highestCardRank = currentRank;
        cardIndex = i;
      }
    }

    return cardIndex;
  }

  findPlayerLowestCard(): number {
    let cardIndex = -1;
    let lowestCardRank = 15;

    for (let i = 0; i < this.cards.length; ++i) {
      const currentRank = this.cards[i].getRank();
      if (lowestCardRank > currentRank) {
        lowestCardRank = currentRank;
        cardIndex = i;
      }
    }

    return cardIndex;
  }

  /**
  * Legnagyobb rtk krtya keresse a megadott sznek kztt
  *
  * @param selectedSuits
  * A megadott krtya sznek amelyekben a keress trtnjen
  *
  * @return
  * A legnagyobb rtk krtya, ha tbb van a megadott priorits szerint vlasztdik
  *
  */
 findHighestCardInSortedCards(selectedSuits: number[]): Card {
  const highCards: Card[] = [];

  selectedSuits.forEach(suitNr => {
    const suitCards = this.sortedCards[suits[suitNr - 1]];
    if (suitCards.length) {
      highCards.push(suitCards[suitCards.length - 1]);
    }
  });

  highCards.sort((a, b) => (a.getRank() < b.getRank()) ? 1 : -1);

  return highCards[0];
}

  /**
  * Legkisebb rtk krtya keresse a megadott sznek kztt
  *
  * @param selectedSuits
  * A megadott krtya sznek amelyekben a keress trtnjen
  *
  * @return
  * A legkisebb rtk krtya, ha tbb van a megadott priorits szerint vlasztdik
  *
  */
  findLowestCardInSortedCards(selectedSuits: number[]): Card {
    const lowestCards: Card[] = [];

    selectedSuits.forEach(suitNr => {
      const suitCards = this.sortedCards[suits[suitNr - 1]];
      if (suitCards.length) {
        lowestCards.push(suitCards[0]);
      }
    });

    lowestCards.sort((a, b) => (a.getRank() > b.getRank()) ? 1 : -1);

    return lowestCards[0];
  }

  getBet(): number {
    return this.bet;
  }

  setBet(bet: number) {
    this.bet = bet;
  }

  getReal(): number {
    return this.real;
  }

  addReal(real: number) {
    this.real += real;
  }

  resetReal() {
    this.real = 0;
  }

  getScore(): number {
    return this.score;
  }

  resetScore() {
    this.score = 0;
  }

  abstract setScore(): void;

  isStarter(): boolean {
    return this.starter;
  }

  setStarter(starter: boolean) {
    this.starter = starter;
  }

  isLast(): boolean {
    return this.last;
  }

  setLast(last: boolean) {
    this.last = last;
  }
}
