import { Table } from './table';
import { HeartsPlayer } from '../player/hearts-player';
import { Card } from '../card/card';
import { Suit } from '../suit/suit';
import { isSpecificCard, spadeQueen } from '../card/cards';

export class HeartsTable extends Table {

  private players: HeartsPlayer[] = [];
  private spadeQueenOut = false;
  private heartsBroken = false;

  constructor(nrOfPlayers: number) {
    super(nrOfPlayers);

    for (let i = 1; i <= nrOfPlayers; ++i) {
      const str = `Player${i}`;
      this.players.push(new HeartsPlayer(str));
    }
  }

  getPlayers(): HeartsPlayer[] {
    return this.players;
  }

  getPlayer(nrOfPlayer: number): HeartsPlayer {
    return this.players[nrOfPlayer];
  }

  setStarterAndLastPlayer(starterPlayer: number) {
    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (i === starterPlayer) {
        this.getPlayer(i).setStarter(true);
      } else {
        this.getPlayer(i).setStarter(false);
      }

      if (i === (this.nrOfPlayers + starterPlayer - 1) % this.nrOfPlayers) {
        this.getPlayer(i).setLast(true);
      } else {
        this.getPlayer(i).setLast(false);
      }
    }

  }

  getStarter(): number {
    let starter = -1;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (this.getPlayer(i).isStarter()) {
        starter = i;
      }
    }

    return starter;
  }

  getLastPlayer(): number {
    let lastPlayer = -1;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (this.getPlayer(i).isLast()) {
        lastPlayer = i;
      }
    }

    return lastPlayer;
  }

  isSpadeQueenOut() {
    return this.spadeQueenOut;
  }

  isHeartsBroken() {
    return this.heartsBroken;
  }

  resetHeartsBroken() {
    this.heartsBroken = false;
    this.spadeQueenOut = false;
  }

  putCardOnTable(playerIdx: number, card: Card) {
    super.putCardOnTable(playerIdx, card);
    if (isSpecificCard(card, spadeQueen)) {
      this.spadeQueenOut = true;
      this.heartsBroken = true;
    }
    if (card.getSuit() === Suit.HEART) {
      this.heartsBroken = true;
    }
  }

  findTurnTaker(): number {
    let playerIndex = -1;
    let highestCardRank = 0;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      const currentSuit = this.cardsOnTable[i].getSuit();
      const currentRank = this.cardsOnTable[i].getRank();
      if (this.firstCard.getSuit() === currentSuit && highestCardRank < currentRank) {
        highestCardRank = currentRank;
        playerIndex = i;
      }
    }

    return playerIndex;
  }

  getCardPoints(): number {
    let points = 0;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      const currentCard = this.cardsOnTable[i];
      if (currentCard.getSuit() === Suit.HEART) {
        points += 1;
      } else if (isSpecificCard(currentCard, spadeQueen)) {
        points += 13;
      }
    }

    return points;
  }
}
