import { Card } from '../card/card';
import { Suit } from '../suit/suit';
import { Player } from './player';

export class RikikiPlayer extends Player {

  constructor(name: string) {
    super(name);
  }

  setScore() {
    let score = 0;
    if (this.bet === this.real) {
      score += 10 + this.real * 2;
    }

    if (this.bet > this.real) {
      score -= (this.bet - this.real) * 2;
    }

    if (this.bet < this.real) {
      score -= (this.real - this.bet) * 2;
    }

    this.score += score;
  }

  addCard(card: Card) {
    super.addCard(card);
    this.sortPlayerCards();
  }

  removeCardByIdx(indexOf: number): Card {
    const removedCard = super.removeCardByIdx(indexOf);
    this.sortPlayerCards();

    return removedCard;
  }

  sortPlayerCards() {
    super.sortPlayerCards([Suit.CLUB, Suit.DIAMOND, Suit.HEART, Suit.SPADE]);
  }
}
