import { Table } from './table';
import { RikikiPlayer } from '../player/rikiki-player';

export class RikikiTable extends Table {

  private players: RikikiPlayer[] = [];
  private betTotal = 0;

  constructor(nrOfPlayers: number) {
    super(nrOfPlayers);

    for (let i = 1; i <= nrOfPlayers; ++i) {
      const str = `Player${i}`;
      this.players.push(new RikikiPlayer(str));
    }
  }

  getPlayers(): RikikiPlayer[] {
    return this.players;
  }

  getPlayer(nrOfPlayer: number): RikikiPlayer {
    return this.players[nrOfPlayer];
  }

  setStarterAndLastPlayer(starterPlayer: number) {
    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (i === starterPlayer) {
        this.getPlayer(i).setStarter(true);
      } else {
        this.getPlayer(i).setStarter(false);
      }

      if (i === (this.nrOfPlayers + starterPlayer - 1) % this.nrOfPlayers) {
        this.getPlayer(i).setLast(true);
      } else {
        this.getPlayer(i).setLast(false);
      }
    }

  }

  getStarter(): number {
    let starter = -1;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (this.getPlayer(i).isStarter()) {
        starter = i;
      }
    }

    return starter;
  }

  getLastPlayer(): number {
    let lastPlayer = -1;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      if (this.getPlayer(i).isLast()) {
        lastPlayer = i;
      }
    }

    return lastPlayer;
  }

  getBetTotal(): number {
    return this.betTotal;
  }

  setBetTotal(betTotal: number) {
    this.betTotal = betTotal;
  }

}
