import { Player } from './player';
import { Card } from '../card/card';
import { cloneDeep } from 'lodash-es';
import { Suit } from '../suit/suit';
import { suits } from '../suit/suits';


export class HeartsPlayer extends Player {

  private startegyTakeAll = false;
  private cardsToSwap: Card[] = [];

  constructor(name: string) {
    super(name);
  }

  setScore() {
    this.score += this.real;
  }

  addCard(card: Card) {
    super.addCard(card);
    this.sortPlayerCards();
  }

  removeCard(cardToRemove: Card) {
    const suit = suits[cardToRemove.getSuit() - 1];
    const properSuitsArray = this.sortedCards[suit];
    this.sortedCards[suit] = properSuitsArray.filter((card: { getRank: () => number; }) => card.getRank() !== cardToRemove.getRank());

    this.sortPlayerCards();
  }

  removeCardByIdx(indexOf: number): Card {
    const removedCard = super.removeCardByIdx(indexOf);
    this.sortPlayerCards();

    return removedCard;
  }

  sortPlayerCards() {
    super.sortPlayerCards([Suit.HEART, Suit.SPADE, Suit.DIAMOND, Suit.CLUB]);
  }

  isStartegyTakeAll(): boolean {
    return this.startegyTakeAll;
  }

  setStartegyTakeAll(takeAll: boolean) {
    this.startegyTakeAll = takeAll;
  }

  addCardToSwap(card: Card) {
    this.cardsToSwap.push(card);
  }

  getCardsToSwap(): Card[] {
    const cardsToSwapTemp = cloneDeep(this.cardsToSwap);
    this.cardsToSwap = [];
    return cardsToSwapTemp;
  }
}
