import { Card } from './card';

export const nullCard = new Card(0, 0);
export const clubTwo = new Card(1, 2);
export const spadeQueen = new Card(4, 12);
export const spadeKing = new Card(4, 13);
export const spadeAce = new Card(4, 14);

export const isSpecificCard = (cardToExamin: Card, cardToCompare: Card) => {
  return cardToExamin.getSuit() === cardToCompare.getSuit() && cardToExamin.getRank() === cardToCompare.getRank();
};
export const isSpecificCardIncluded = (cards: Card[], cardToFind: Card) => {
  return cards?.filter(card => isSpecificCard(card, cardToFind)).length;
};
