import { Card } from '../card/card';
import { nullCard } from '../card/cards';
import { Deck } from '../deck/deck';

export class Table {
  private deck: Card[] = [];
  protected nrOfPlayers: number;
  protected cardsOnTable: Card[] = [];
  protected firstCard = nullCard;

  constructor(nrOfPlayers: number) {
    this.nrOfPlayers = nrOfPlayers;

    const deckOfCards = new Deck();
    this.deck = deckOfCards.getDeck();
    this.initCardsOnTable();
  }

  initCardsOnTable() {
    for (let i = 0; i < this.nrOfPlayers; i++) {
      this.cardsOnTable[i] = nullCard;
    }
  }

  shuffleDeck() {
    Deck.shuffle(this.deck);
  }

  getDeck() {
    return this.deck;
  }

  getCardFromDeck(): Card {
    return this.deck.splice(0, 1)[0];
  }

  addCardToDeck(card: Card) {
    this.deck.push(card);
  }

  getTrumpCard(): Card {
    return this.getCardFromDeck();
  }

  nrOfCardsOnTable(): number {
    let nrOfCards = 0;
    for (let i = 0; i < this.cardsOnTable.length; i++) {
      if (!!this.cardsOnTable[i].getSuit()) {
        ++nrOfCards;
      }
    }
    return nrOfCards;
  }

  getFirstCard(): Card {
    return this.firstCard;
  }

  checkCardSlotsOnTable(): Card[] {
    return this.cardsOnTable;
  }

  checkCardsOnTable(): Card[] {
    const cardsOnTable: Card[] = [];
    for (let i = 0; i < this.cardsOnTable.length; i++) {
      if (this.cardsOnTable[i].getSuit()) {
        cardsOnTable.push(this.cardsOnTable[i]);
      }
    }
    return cardsOnTable;
  }

  checkCardOnTable(indexOf: number): Card {
    return this.cardsOnTable[indexOf];
  }

  isTableEmpty(): boolean {
    return !this.cardsOnTable.filter(card => card !== nullCard).length;
  }

  putCardOnTable(playerIdx: number, card: Card) {
    if (!this.firstCard.getSuit()) {
      this.firstCard = card;
    }
    this.cardsOnTable.splice(playerIdx, 1, card);
  }

  removeCardsFromTable() {
    this.deck = [...this.deck, ...this.checkCardsOnTable()];
    this.initCardsOnTable();
    this.firstCard = nullCard;
  }

  findHighestCardOnTable(trumpCardSuit: number): number {
    let cardIndex = -1;
    let highestCardRank = 0;

    for (let i = 0; i < this.nrOfPlayers; ++i) {
      const currentSuit = this.cardsOnTable[i].getSuit();
      const currentRank = this.cardsOnTable[i].getRank();
      if (trumpCardSuit === currentSuit && highestCardRank < currentRank) {
        highestCardRank = currentRank;
        cardIndex = i;
      }
    }

    if (cardIndex === -1) {
      for (let i = 0; i < this.nrOfPlayers; ++i) {
        const currentSuit = this.cardsOnTable[i].getSuit();
        const currentRank = this.cardsOnTable[i].getRank();
        if (this.firstCard.getSuit() === currentSuit && highestCardRank < currentRank) {
          highestCardRank = currentRank;
          cardIndex = i;
        }
      }
    }

    return cardIndex;
  }
}
